



//about me
.me {
    color: $secundary;
    @include flexCenterisation;
    padding: 20px; 
}
.about-me{
    padding: 80px;
    display: grid;
    grid-template-columns: repeat(2,1fr);
    justify-items: center;
    background-color: $primary;
    gap: 2em;
    @include mobile{
        padding: 30px;
        display: flex;
        flex-direction: column;
    }
    .photo{
        width: 100%;
        height: 100%;
        img{
            width: 100%;
            height: 100%;
        }
    }
    h2{
        grid-column: span 2;
        justify-self: center;
    }
    .about-txt{
        p{
            margin: 10px 0;
            font-size: 1.2em;
        }
        @include mobile{
            font-size: 0.9em;
        }
        a{
            text-decoration: none;
            color: $blu2;
        }
    }

    
}
//folio
.index{
    .folio-bg{
        text-align: center;
        padding: 20px;      
        @include flexCenterisation;
    }
    .portfolio{
        width: 100%;
        border-radius: 20px;
        overflow: hidden;
        background-color: $primary;
        h2{
            color: $secundary;
        }
    }
    .folio-title{
        display: flex;
        overflow: hidden;
        @include mobile {
            flex-direction: column;
        }
        >div{
            @include flexCenterisation;
            flex-direction: column;
            width: 100%;
            height: 500px;
            transition: all 0.5s ease-in-out;
            overflow: hidden;
            @include aboveTablet{
                &:hover{
                    width: 300%;
                    transition: all 0.2s ease-in-out;
                }
            }
            @include mobile{
                height: 300px;
            }
            h3{
                font-size: 2em;
            }
            .folio-txt{
                background-color: rgba(255, 0, 0, 0.596);
                padding: 40px;
                border-radius: 40px;
            }
        }
        .web{
            background: url(../media/img/florian-olivo-unsplash.jpg) center/cover; 
            h3{
                color: $secundary;
            }
        }
        .news{
            background: url(../media/img/brett-jordan-unsplash.jpg) center/cover; 
            h3{
                color: $primary;
            }
            a{
                background-color: $primary;
                color: $secundary;
                border: 4px solid $primary;
                &:hover{
                    background-color: $secundary;
                    color: $primary;
                }
            }
        } 
    }
}
//compétences
.comp-bg{
    @include flexCenterisation;
    padding: 20px;
    @include mobile{
        display: block;
    }  
    .comp-content{
        width: 100%;
        height: 70%;
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        justify-items: center;
        gap: 1em;
        background-color: $primary;
        .comp-title{
            grid-column: span 3;
            justify-self: center;
            color: $secundary;
        }
        a{
            &:nth-of-type(7){
                grid-column: span 3;

            }
            img{
                transition: 0.3s all ease-in-out;
            }
            &:hover{
                img{
                    transform: scale(140%);
                    transition: 0.2s all ease-in-out;
                }
            }
        }
    }

}
