//-----------------------------------------------//
// mixin pour media-querry

$mobile:764px;
$tablet: 900px;
$lowres : 1500px;


@mixin mobile {
    @media screen and (max-width: $mobile) {
        @content;
    }
}
@mixin lowres {
    @media screen and (max-width: $lowres) {
        @content;
    }
}
@mixin tablet {
    @media screen and (max-width: $tablet) {
        @content;
    }
}
@mixin aboveTablet {
    @media screen and (min-width: $tablet) {
        @content;
    }
}
//-----------------------------------------------//

//pos absolute center//
@mixin centerisation {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
}
//flex center//
@mixin flexCenterisation {
    display: flex;
    justify-content: center;
    align-items: center;
}
