.languages, .framework, .cms{
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 2em;
    padding: 20px;
    article{
        width: 33%;
        background-color: $primary;
    }
    h2{
        text-align: center;
        color: $secundary;
        font-size: 2em;
        text-transform: uppercase;
    }
    li{
        color: $secundary;
        margin: 10px 0;
    }
    @include mobile{
        flex-direction: column;
        article{
            width: 100%;
        }
    }
}