//Portfolio
.folio{
    .portfolio{
        height: 100vh;
        @include flexCenterisation;
        padding: 20px;
        @include tablet {
            height: auto;
        }
        @include mobile{
            width: 100%;
            height: auto;
            flex-direction: column;
        }
        .exo-copy, .exo-newsletter{
            width: 100%;
            height: 80%;
            border-radius: 20px;
            display: grid;
            grid-template-columns: repeat(2, 1fr);
            grid-template-rows: 300px 50px 300px;
            align-content: center;
            justify-items: center;
            align-items: center;
            background-color: $primary;
            @include mobile{
                display: flex;
                flex-direction: column;
                gap: 2em;
                padding-bottom: 2em;
            }
        }
        .folio-title{
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            gap: 2em;
            color: $secundary;
            grid-row: 2;
            grid-column: span 2;
            p{
                text-align: center;
                width: 150px;
                @include mobile{
                    display: none;
                }
            }
        }
        article{
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            gap: 1em;
            overflow: hidden;
            border-radius: 20px;
            background-color: $primary;
            padding-bottom: 50px;
            -webkit-box-shadow: -10px 0px 13px -7px #000000, 10px 0px 13px -7px #000000, 5px 5px 15px 5px rgba(0,0,0,0); 
            box-shadow: -10px 0px 13px -7px #000000, 10px 0px 13px -7px #000000, 5px 5px 15px 5px rgba(0,0,0,0);
        }
        .content-big{
            display: none;
            position: fixed;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            padding: 80px;
            iframe { 
                background-color: #fff;
            }
            &:before{
                content: "";
                position: absolute;
                top: 0;
                left: 0;
                height: 100vh;
                width: 100%;
                background-color: rgba(0, 0, 0, 0.699);
                z-index: -1;
            }
        }
        .show{
            display: block;
        }
        .content-small{
            width: 300px;
            height: 100%;
            img{
                width: 100%;
            }
            &:hover{
                cursor: pointer;
            }
        }
    }
    //la petite croix pour fermer la modal
    .btn-close {
        position: absolute;
        right: 25px;
        top: 50px;
        width: 50px;
        height: 50px;
        opacity: 0.5;
        &:hover {
            opacity: 1;
            cursor: pointer;
        }
        &:before, &:after {
            position: absolute;
            left: 15px;
            content: ' ';
            height: 50px;
            width: 4px;
            background-color: rgb(255, 255, 255);
        }
        &:before {
            transform: rotate(45deg);
        }
        &:after {
            transform: rotate(-45deg);
        }
        
    }
    
}

