.header-nav-menu{
    width: 100%;
    background-color: $primary;
    padding: 5px;
    position: fixed;
    .wrapper {
        display: flex;
        align-items: center;
        gap: 2em;
    }
    z-index: 1;
    @include mobile{
        position: relative;
    }
}
.logo{
    position: absolute;
    width: 200px;
    height: 150px;
    top: 40px;
    background-color: $primary;
    border-bottom-left-radius: 50%;
    border-bottom-right-radius: 50%;
    img{
        display: block;
        width: 100%;
    }
}