.footer{
    width: 100%;
    position: fixed;
    bottom: 0;
    z-index: 1;
    background-color: $primary;
    align-items: center;
    justify-content: center;
    display: flex;
    padding: 0 20px;
    .footer-txt{
        display: flex;
        gap: 2em;
        word-spacing: 0.1em;
        p{
            color: $secundary;
        }
        span{
            font-style: italic;
        }
        a{
            color: $blu2;
            text-decoration: none;
        }
    }
    @include tablet {
        position: relative;
    }
}