// Bouton pour revenir au top 
.to-top{
    display: flex;
    justify-content: center;
    align-items: center;
    position: fixed;
    right: 50px;
    bottom: 50px;
    width: 50px;
    height: 50px;
    background-color: $secundary;
    opacity: 0;
    transition: all 1s ease;
    div{
        width: 20px;
        height: 20px;
        border: solid black;
        border-width: 0 3px 3px 0;
        display: inline-block;
        transform: rotate(-135deg);
    }
}
// bouton show more

.more{
    display: block;
    height: 40px;
    width: 100px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: $secundary;
    border: 4px solid $secundary;
    border-radius: 10px;
    color: $primary;
    transition: 0.5s all ease-in-out;
    text-decoration: none;
    font-size: 1.2em;
    &:hover{
        background-color: $primary;
        color: $secundary;
    }
}