

.main-nav, .hide-nav{
    display: flex;
    gap: 2em;
    a{
        display: flex;
        justify-content: center;
        align-items: center;
        padding: 10px ;
        text-decoration: none;
        color: $secundary;
    }
}
.hide-nav{
    display: none;
}
@include lowres{
    .header-nav-menu{
        .wrapper{
            justify-content: space-around;
            .main-nav, .hide-nav{
                max-width: 100%;
            }
            .hide-nav{
                display: flex;
                @include mobile{
                    display: none;
                }
            }
            @include mobile{
                justify-content: flex-start;
            }
        }
    }
}


.active { 
    background-color: $blu2;
}


.side-bar{
    width: 350px;
    position: fixed;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 2em;
    background-color: rgba(0, 0, 0, 0.733);
    top: 0;
    bottom: 0;
    .side-nav{
        height: 50%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        gap: 2em;
        a{
            text-decoration: none;
            color: $secundary;
        }   
    }
    .inner-main-nav{
        position: relative;
        text-transform: uppercase;
        font-size: 0.9em;
        transition: 1s all ease-in-out;
        &:after{
            content: "";
            position: absolute;
            bottom: 0;
            left: 0;
            width: 0;
            height: 2px;
            background-color: $blu2;
            transition: 1s all ease-in-out;
        }
    }
    
    .side-active{
        border-width: 2px;
        font-size: 1.5em;
        transition: 1s all ease-in-out;
        &:after{
            width: 100%;
            transition: 1s all ease-in-out;
        }
    }
    @include lowres{
        display: none;
    }
}


