body{
    font-family: 'Montserrat', sans-serif;
}
html {
    scroll-behavior: smooth;
  }
.wrapper { 
    max-width: 1080px;
    margin: 0 auto;
}
h1,h2,h3,h4,h5,h6{
    font-family: 'Raleway', sans-serif;
}
h2{
    font-size: 2.5em;
}
img{
    display: block;
}
body{
    overflow: hidden;
    background: url(../media/img/custombg.png) no-repeat center/cover fixed;
}
.box-and-shadow{
    border-radius: 20px;
    -webkit-box-shadow: -10px 0px 13px -7px #000000, 10px 0px 13px -7px #000000, 5px 5px 15px 5px rgba(0,0,0,0); 
    box-shadow: -10px 0px 13px -7px #000000, 10px 0px 13px -7px #000000, 5px 5px 15px 5px rgba(0,0,0,0);
}
section{
    height: 100vh;
    @include mobile{
        height: auto;
    }
}
//titre
.title-content{
    width: 100%;
    height: 100vh;
    @include flexCenterisation;
    flex-direction: column;
    padding: 20px;  
    @include mobile{
        height: 50%;
        padding-top: 50px;
        text-align: center;
    }
    .title{
        font-size: 3em;
        color: $secundary;
        h1{
            margin: 5px;
            transform: translateX(-100%);
            animation: leftToRight forwards 0.5s ;
        }
    }
    .txt{
        text-align: center;
        font-size: 2em;
        color: $secundary;
        overflow: hidden;
        p{
            &:nth-of-type(1){
               transform: translateX(100%);
               animation: rightToLeft forwards 0.5s 0.5s ;
            }
            &:nth-of-type(2){
                transform: translateX(-100%);
                animation: leftToRight forwards 0.5s 1s ;
            }
        }
    }
    @include mobile{
        .title{
            font-size: 2em;
        }
        .txt{
            font-size: 1.5em;
        }
    }
}
@include tablet{
    body{
        overflow: auto;
    }
}
